}

.titleText {
    margin: 1.5em;
    font-size: 1.8rem;
}

.full-height {
    height: 100%;
}
.Nav {
    background-color: #110E43;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: auto;
}

.navbarButton {
    cursor: pointer;
    display: block;
    padding: 8px 16px;
    margin-left: auto;
    margin-right: auto;
    background: transparent;
    border: none;
    color: #fff;
    width: 100%
}

.marginButtons {
    margin: 10px 10px;
    font-family: 'Lexend';
}

.activeNavbarBtn {
    background-color: #4940D7;
}

.navbarItemTxt {
    display: inline-block;
    margin-top: 0.75rem;
    margin-bottom: 0.5rem;
}

.userIcon {
    background-color: #00F287;
    width: 2em;
    height: 2em;
    margin: auto;
    margin-top: 1em;
    margin-bottom: 1em;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-weight: 600;
    font-size: 30px;
    color: white;
    cursor: pointer;
}
.mainPlayersComponent {
    background-color: #FBFBFF;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: auto;
    border-right: 2px solid #EEEEF8;
}

.playersNavTitle {
    font-size: 2rem;
    margin-bottom: 10%;
}

.playersTitle {
    margin: 10%;
}

.searchPlayersNav {
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/0/0b/Search_Icon.svg');
    background-size: 1.45rem;
    background-position-x: 1rem;
    background-position-y: center;
    background-repeat: no-repeat;
    padding: 1em;
    padding-left: 3rem;
    width: 85%;
    height: 80%;
    border-radius: 8px;
    border: 1px solid #E9E9F3;
    box-sizing: border-box;
}

.mainDiv {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    gap: 10px;
    grid-auto-rows: minmax(100px, auto);
}
.playerListItem {
    display: inline-flex;
    width: 100%;
    height: 10%;
    background-color: #FBFBFF;
    vertical-align: middle;
    cursor: pointer;
    border-bottom: 2px solid #E9E9F3;
}

.playerListItemActive {
    display: inline-flex;
    width: 100%;
    height: 10%;
    background-color: #ECECFF;
    vertical-align: middle;
    border-bottom: 2px solid #E9E9F3;
}

.playerListImg {
    height: 3.5rem;
    -webkit-clip-path: circle();
            clip-path: circle();
    margin-left: 2rem;
    margin-top: auto;
    margin-bottom: auto;
    background-color: #CBE3FF;
}

.playerListName {
    margin-top: auto;
    margin-bottom: auto;
    font-size: 0.9vw;
    margin-left: 5%;
}

.text-active {
    color: #060515;
    font-weight: 600;
}

.text-non-active {
    color: #060515;
    font-weight: 400;
}
.playerTitle {
    display: inline-flex;
    align-items: center;
    margin: 3%;
}

.mainPlayerInfoDiv {
    height: 100%;
}

.playerLogo {
    -webkit-clip-path: circle();
            clip-path: circle();
    height: 5rem;
    width: 5rem;
    object-fit: contain;
    background-color: #CBE3FF;
}

.playerName {
    display: inline-block;
    font-size: 1.5rem;
    margin-left: 1rem;
    width: 15rem;
    font-weight: 600;
}

.countryIcon {
    height: 2rem;
    margin-left: 2.5%;
}


.boxOfSomethingIdk {
    display: inline-flex;
    align-items: center;
    background-color: #F3F3FA;
    border-radius: 8px;
    padding: 7px 13px;
    margin-left: 1rem;
}

.textOfSomethingIdk {
    opacity: 0.5;
    font-weight: 500;
    font-size: 0.9rem;
}

.playerBasicInfo {
    display: flex;
    font-weight: 500;
    margin-left: 2rem;
    margin-top: 1rem;
}

.firstLine {
    flex: 1 1;
}

.describingTextBasic {
    font-size: 1.5rem;
    font-weight: 500;
    display: inline-block;
}

.dividedPlayerInfo {
    display: inline-flex;
    margin-right: 5%;
    width: 40%;
    font-size: 1.2rem;
    margin-left: 2rem;
    margin-top: 1rem;
}
  
.dividerPlayerInfo {
    flex-grow: 1;
    border-bottom: 3px dotted #E9E9F3;
}


.secondLine {
    overflow: hidden;
}
