.subProgressBar {
    height: 0.55em;
    background-color: #00F287;
    border-radius: 10px;
    animation: progressBarEnterAnimation 1000ms;
    transition-duration: 750ms;
}

@keyframes progressBarEnterAnimation {
    from {
        width: 0;
    }
    to {
        width: 100%;
        width: var(--w, 100%);
    }
}

.progressBar {
    height: 0.55em;
    background-color: #F7F7F8;
    border-radius: 10px;
}

.progressBarTitle {
    font-family: 'Lexend';
    display: inline;
    font-weight: 400;
}

.teamWork {
    width: 100%;
    padding: 1em;
}

.percentsOfProgressBar {
    display: inline;
    float:right;
    font-weight: 400;
}

.pitchImg {
    margin: 0 auto;
    height: 10em;
}


.selectionsDiv {
    font-family: 'Lexend';
    margin: 0 auto;
    margin-bottom: 3em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

